import styled from 'styled-components'
export const DetailWrapper = styled.div`
overflow:hidden;
width:750px;
margin:0 auto;
padding-bottom:100px;

`
export const Header = styled.div`
margin:50px 0 20px 0;
line-height:44px;
font-size:34px;
text-align:center;
color:#333;

font-weight:bold;img{
    width:620px;
    margin: 0 auto;
    margin-top:50px;
}
`
export const Content = styled.div`
color:#2f2f2f;

p{
    margin:20px 0;
    font-size:16px;
    line-height:30px;

}
`


